// 
<template>
  <div>
    <div>
      <crumbs menu='' subMenu='' /> 
      <div class=" container sql">
          <el-row type="flex" justify="space-around">
          <el-col :span=6>
            <market @marketGet='marketGet' :marketSet='marketName'/>
          </el-col>
          <el-col :span='6'  >
            :
            <el-select v-model="indexName" placeholder="" filterable>
              <el-option
                v-for="type in indexType" :label="type.key" :value="type.value" :key="type.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span='6'>
            :
           <el-select v-model="areaName" placeholder="" filterable>
              <el-option
                v-for="type in regionType" :label="type.key" :value="type.value" :key="type.value">
              </el-option>
            </el-select>
          </el-col>
            <el-col :span='6'>
            <el-button type="primary" icon="el-icon-search" @click='onQuery'></el-button>
            <el-button type="primary" icon="el-icon-refresh-right" @click="reSet"></el-button>
            <!-- <el-button type="primary" icon="el-icon-document"></el-button> -->
          </el-col>
        </el-row>    
      </div>
      <el-card class="marginTop10" shadow="hover">
                  <el-row>
                      <el-table class="font-set"
                        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                        border
                        @sort-change="sort_change"
                        :current-page="currentPage"              
                        stripe
                      >
                      <el-table-column
                        prop="rank"
                        label=""
                        sortable
                        align="center"
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop="area"
                        label=""
                        align="center"
                      
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop='data'
                        :label="indexName"
                        align="center"
                        sortable
                        min-width="20%">
                      </el-table-column>
                      <el-table-column
                        prop="market"
                        label=""
                       
                        align="center"
                        min-width="20%">
                      </el-table-column>
                    </el-table>
            </el-row>
              <div class="pagination flexCenter" >
                  <el-pagination 
                      class="flexCenter"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :page-sizes="[10,30,50]"
                      :page-size="pagesize"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="tableData.length">     
                  </el-pagination>
              </div>
    </el-card>
    </div>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import axios from 'axios'
export default {
  data(){
    return {
      indexName:'',
      marketName:'',
      areaName:'',
      currentPage: 1,
			pagesize:10,
      tableData:[],
      temList:[],
      indexType: [
        { key: '', value: '' },
        { key: '', value: '' }
      ],
      regionType: [
        { key: '', value: '' },
        { key: '1', value: '1' },
        { key: '2', value: '2' },
        { key: '11', value: '11' },
        { key: '12', value: '12' },
        { key: '21', value: '21' },
        { key: '22', value: '22' },
        { key: '', value: '' }
      ],
      formInline: {
        page: 1,
        limit: 10,
        machineNo: '',
        orderNo: '',
        transId: '',
        indexType: 0,
        orderStatus: 0,
        marketName:'',
        token: localStorage.getItem('logintoken')
      },
      
    }
  },
  components:{
    market:MarketSelect,
    crumbs:Crumbs,

  },
  mounted(){
    axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/realtimeIndexRanking').then(res=>{
      console.log(res.data.list)
      let sortList= res.data.list.sort((a,b)=>{ return b.flow-a.flow})
      for(let i=0;i<sortList.length;i++){
        sortList[i].rank=i+1
      }
      let newArry=[]
      for(let i=0;i<sortList.length;i++){
        newArry.push({
          rank:sortList[i].rank,
          data:sortList[i].flow,
          market:this.marketName,
          area:sortList[i].area
        })
      }
      console.log(newArry)
      this.tableData=[...newArry]
    })
  },
  methods:{
    marketGet(val){
      this.marketName=val
    },
     sort_change(params){  
        console.log(params)
        if(params.order=="ascending"){
          this.tableData.sort((a,b)=>a[params.prop]-b[params.prop])
        }else if(params.order=="descending"){
          this.tableData.sort((a,b)=>b[params.prop]-a[params.prop])
        }else {
          this.tableData.sort((a,b)=>a.rank-b.rank)
        }
        
      },
    handleSizeChange(val) {
          this.pagesize=val;
      },
      handleCurrentChange(val) {
          this.currentPage = val;
      },
    onQuery(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/realtimeIndexRanking').then(res=>{
      let sortList=res.data.list
      if(this.areaName!=''){
        sortList=sortList.filter(item=>item.area==this.areaName)
      }
      if(this.indexName==''){
        sortList=sortList.sort((a,b)=>{ return b.flow-a.flow})
      }else{
        sortList=sortList.sort((a,b)=>{ return b.sale-a.sale})
      }
      for(let i=0;i<sortList.length;i++){
        sortList[i].rank=i+1
      }
      let newArry=[]
      for(let i=0;i<sortList.length;i++){
        newArry.push({
          rank:sortList[i].rank,
          data:this.indexName==''?sortList[i].flow:sortList[i].sale,
          market:this.marketName,
          area:sortList[i].area
        })
      }
      this.tableData=[...newArry]  
    })
    },
    reSet(){
      this.indexName=''
      this.areaName='',
      this.marketName=''
      this.onQuery()
    }
  },
  computed:{
    _fileName(){
      return `${this.marketName}-${this.indexName}-${this.areaName}-${this.$moment().format('YYYYMMDD')}`
    }
  }
  
}
</script>
<style lang="css" scoped>
.borderSet{
    /* border-right: 2px solid black; */
    border-left: 2px solid #C0C4CC;
  }
  .flexCenter{
    display: flex;
    align-items: center; /**/
    justify-content: flex-end;  /**/
    /* flex-wrap: wrap; */
  }
  .flexJus{
    display: flex;
    justify-content: space-around;  /**/
  }
 .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .card-header span{
    font-size: 20px;
    font-weight: bolder;
  }
  .fontSet{
     font-size: 18px;
    font-weight: bolder;
  }
  .fontCenter{
    display: flex;
    align-items: center; /*  */
    justify-content: space-around;

  }
</style>